.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-breadcrumbList {
  display: flex;
  flex-flow: row;
  margin-bottom: 16px;
}
.RA-breadcrumb {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  padding: 0 10px 0 20px;
  cursor: pointer;
  color: var(--text-color-secondary);
  font-weight: 400;
}
.RA-breadcrumb.RA-breadcrumb-display {
  color: var(--heading-color);
}
.RA-breadcrumb:hover {
  padding: 0 40px 0 25px !important;
}
.RA-breadcrumb:hover .RA-breadcrumb-closeIcon {
  opacity: 1;
  right: 8px;
}
.RA-breadcrumb::before {
  content: '';
  border: 1px solid;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  position: absolute;
  height: 15px;
  left: 5px;
  top: 1px;
  color: var(--text-color-secondary);
}
.RA-breadcrumb:last-child {
  padding: 0 20px;
}
.RA-breadcrumb:last-child .RA-breadcrumb-closeIcon {
  right: 20px;
}
.RA-breadcrumb:last-child:hover {
  padding: 0 55px 0 25px !important;
}
.RA-breadcrumb:last-child::after {
  content: '';
  border: 1px solid;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  position: absolute;
  height: 15px;
  right: 5px;
  top: 1px;
  color: var(--text-color-secondary);
}
.RA-breadcrumb-closeIcon {
  position: absolute;
  top: 3px;
  right: 20px;
  color: var(--primary);
  opacity: 0;
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  padding: 8px;
  margin-top: -8px;
  margin-right: -8px;
}
