.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-selectLang-iconContainer {
  padding: 0 12px;
  height: 100%;
  line-height: 60px;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  color: var(--text-color);
}
.RA-selectLang-iconContainer:hover {
  background: rgba(0, 0, 0, 0.025);
}
.RA-selectLang-iconContainer:hover .RA-selectLang-icon {
  color: var(--primary);
}
.RA-selectLang-iconContainer .RA-selectLang-icon {
  color: var(--text-color);
  margin-top: 4px;
  font-size: 16px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
