.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-loading {
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  position: absolute;
  z-index: 999;
}
.RA-loading::after {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: var(--body-background);
  opacity: 0.5;
  -webkit-filter: blur(2px);
          filter: blur(2px);
  z-index: -1;
}
.RA-loading.RA-loading-hide {
  z-index: -1;
  opacity: 0;
  -webkit-transition: opacity ease-in-out 0.4s, z-index 0.6s ease 0.1s;
  transition: opacity ease-in-out 0.4s, z-index 0.6s ease 0.1s;
}
.RA-loading.RA-loading-fixed {
  left: 220px;
  top: 60px;
  bottom: 0;
  right: 0;
  position: fixed;
}
.RA-loading.RA-loading-fixed.RA-loading-collapsed {
  -webkit-transition: none;
  transition: none;
  left: 80px;
}
.RA-loading .RA-loading-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-flow: column;
  align-items: center;
}
.RA-loading .RA-loading-content span {
  display: block;
  margin-top: 20px;
  font-size: 18px;
  color: var(--primary-lightener);
  letter-spacing: 2px;
}
.RA-loading .RA-loading-content-circle {
  height: 80px;
  width: 80px;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(25%, var(--primary-lightener)), to(var(--primary-darken)));
  background-image: linear-gradient(to right, var(--primary-lightener) 25%, var(--primary-darken));
  border-radius: 50%;
  padding: 5px;
  box-shadow: 0px 0px 5px var(--primary-lightener);
  will-change: transform;
  -webkit-animation: roll 1s linear infinite forwards;
          animation: roll 1s linear infinite forwards;
}
.RA-loading .RA-loading-content-circle::after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  background: var(--body-background);
  box-shadow: 0px 0px 5px var(--primary-lightener);
}
.RA-loading .RA-loading-content-circle::before {
  content: '';
  display: block;
  position: absolute;
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  border: 5vh solid var(--body-background);
  border-top: 5vh solid transparent;
  border-radius: 50%;
}
@-webkit-keyframes roll {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes roll {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
