.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-header {
  color: #ffffff;
  height: 60px;
  background: var(--component-background);
  position: relative;
  box-sizing: border-box;
  text-align: left;
  box-shadow: 4px 4px 40px 0 rgba(0, 0, 0, 0.05);
  z-index: 2;
  display: flex;
  align-items: center;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  width: 100%;
  flex: 0 0 auto;
}
.RA-header.RA-header-isMobile {
  left: 0;
  width: 100% !important;
}
.RA-header .RA-header-foldIcon {
  font-size: 18px;
  margin-left: 32px;
  color: #666;
  cursor: pointer;
}
.RA-header .RA-header-rightPlace {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  min-width: 180px;
}
.RA-header.RA-header-vertical.RA-header-darkTheme {
  box-shadow: 0px 4px 40px 0 #000000 !important;
}
.RA-header.RA-header-vertical.RA-header-inlineLayout {
  left: 0 !important;
  justify-content: unset;
  box-shadow: 4px 4px 6px 0 rgba(0, 0, 0, 0.05);
  z-index: 4;
}
.RA-header.RA-header-vertical.RA-header-inlineLayout.RA-header-fixHeader {
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
}
.RA-header.RA-header-vertical.RA-header-splitLayout.RA-header-withoutMenu {
  width: 100% !important;
}
.RA-header.RA-header-vertical.RA-header-splitLayout.RA-header-fixHeader {
  position: fixed;
  width: calc(100% - 255px);
  top: 0;
  right: 0;
}
.RA-header.RA-header-vertical.RA-header-splitLayout.RA-header-fixHeader.RA-header-collapsed {
  width: calc(100% - 80px);
}
.RA-header.RA-header-horizontal {
  left: 0px !important;
}
.RA-header.RA-header-horizontal > .RA-header-container {
  max-width: 1250px;
  margin: auto;
  position: relative;
  display: flex;
  flex: 1 1;
  max-height: 60px;
  width: 100%;
}
.RA-header.RA-header-horizontal > .RA-header-container.RA-header-container-flowMode {
  max-width: unset;
}
.RA-header.RA-header-horizontal > .RA-header-container .RA-header-rightPlace {
  position: relative;
}
.RA-header.RA-header-horizontal > .RA-header-container .RA-header-headerNav {
  flex: 1 1;
  min-width: 0;
}
.RA-header.RA-header-horizontal > .RA-header-container .siteDetail {
  justify-self: start;
}
.RA-navigator {
  min-height: 100%;
  background-color: var(--component-background);
  box-shadow: 0px 0px 28px var(--primary-lightener-extra);
  z-index: 3;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  display: flex;
  flex-flow: column;
  flex: 0 0 255px;
  max-width: 255px;
  min-width: 255px;
  width: 255px;
  position: relative;
  overflow: hidden;
}
.RA-navigator.RA-navigator-fixSiderBar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
}
.RA-navigator.RA-navigator-collapsed {
  flex: 0 0 80px;
  max-width: 80px;
  min-width: 80px;
  width: 80px;
}
.RA-navigator.RA-inlineLayout-navigator.RA-navigator-fixSiderBar {
  top: 60px;
  height: calc(100vh - 88px);
  min-height: unset;
}
.RA-navigator.RA-splitLayout-navigator {
  z-index: 8;
}
.RA-menu {
  margin-top: 24px;
  flex: 1 1;
  overflow: auto;
  overflow-x: hidden;
  border: 0 !important;
}
.RA-menu::-webkit-scrollbar {
  display: none !important;
}
.RA-menu .RA-menuIcon {
  margin-right: 12px !important;
  display: inline-block;
}
.RA-menu .RA-menuIcon > span {
  margin: 0 !important;
}
.RA-menu.RA-menu-horizontal {
  margin-top: 0;
  line-height: 60px;
  height: 60px;
}
.RA-menu.RA-menu-horizontal .RA-antd-subMenu-title,
.RA-menu.RA-menu-horizontal .RA-antd-menuItem-title {
  border: 0 !important;
  height: 100%;
  line-height: 60px;
  top: 0px !important;
}
.RA-menu.RA-menu-horizontal .RA-antd-menuItem-title {
  padding: 0 20px;
}
.RA-menu.RA-menu-horizontal .RA-antd-subMenu-title > div {
  height: 60px;
}
.RA-menu.RA-menu-collapsed .RA-antd-subMenu-title,
.RA-menu.RA-menu-collapsed .RA-antd-menuItem-title {
  display: inline-block;
  max-width: 0;
  opacity: 0;
}
.RA-menu.RA-menu-collapsed .RA-menuIcon {
  display: block;
  min-width: 14px;
}
.RA-menu .RA-antd-subMenu,
.RA-menu .RA-antd-menuItem {
  -webkit-transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), background 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), background 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.RA-siteDetail {
  width: 100%;
  height: 60px;
  line-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 1px 9px -3px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.RA-siteDetail.RA-siteDetail-horizontal {
  justify-content: unset;
  min-width: 160px !important;
}
.RA-siteDetail.RA-siteDetail-inlineLayout {
  width: auto;
  min-width: 255px;
  box-shadow: none;
}
.RA-siteDetail.RA-siteDetail-darkTheme {
  box-shadow: 0 1px 9px -3px #000000;
}
.RA-siteDetail.RA-siteDetail-collapsed .RA-siteDetail-title {
  display: none;
}
.RA-siteDetail .RA-siteDetail-logo {
  height: 45px;
}
.RA-siteDetail .RA-siteDetail-title {
  line-height: 60px;
  font-size: 18px;
  font-weight: 700;
  color: var(--primary);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-left: 8px;
}
.RA-userInfo {
  color: var(--text-color);
  display: flex;
  font-size: 14px;
  height: 100%;
}
.RA-userInfo > span {
  font-weight: bold;
}
.RA-userInfo .RA-userInfo-menuItem {
  line-height: normal;
  line-height: initial;
  margin-left: 8px;
}
.RA-userInfo .RA-userInfo-icon {
  font-size: 18px;
  font-weight: bold;
  margin-right: 8px;
}
.RA-userInfo .RA-userInfo-dropdown {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 8px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  line-height: 60px;
}
.RA-userInfo .RA-userInfo-dropdown:hover {
  background: rgba(0, 0, 0, 0.025);
  color: var(--primary);
}
.RA-userInfo .RA-userInfo-userName {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width: 600px) {
  .RA-userInfo .RA-userInfo-userName {
    width: 50px;
  }
}
