.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-result {
  text-align: center;
  width: 75%;
  margin: 0 auto;
}
.RA-result-icon {
  font-size: 72px;
  margin-bottom: 24px;
  color: #00ce68 !important;
}
.RA-result-icon.RA-result-icon-failed {
  color: #e65251 !important;
}
.RA-result-title {
  font-size: 24px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  line-height: 32px;
  margin-bottom: 16px;
}
.RA-result-subTitle {
  font-size: 14px;
  line-height: 22px;
  color: var(--text-color-secondary);
  margin-bottom: 24px;
}
.RA-result-extra {
  background: #fafafa;
  padding: 24px 40px;
  border-radius: 2px;
  text-align: left;
}
.RA-result-actions {
  margin-top: 32px;
}
