.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-pageWrapper {
  margin: -24px;
  display: flex;
  flex-flow: column;
}
.RA-pageWrapper .RA-pageWrapper-body {
  padding: 24px;
  flex: 1 1;
}
.RA-pageWrapper .RA-pageWrapper-header {
  background: var(--popover-background);
  padding: 16px 24px 16px;
  box-shadow: 4px 4px 6px 0 rgba(0, 0, 0, 0.05);
  position: relative;
}
.RA-pageWrapper .RA-pageWrapper-header.RA-pageWrapper-header-withoutBody {
  background: transparent;
  box-shadow: none;
  padding-bottom: 0;
  margin-bottom: -12px;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-title {
  margin-bottom: 0px;
  color: var(--heading-color);
  font-weight: 500;
  font-size: 20px;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-subTitle {
  color: var(--text-color);
  font-size: 14px;
  font-weight: 400;
  margin-top: 16px;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-row {
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  align-items: center;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-title,
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-content {
  flex: auto;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-title,
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-subTitle {
  flex: 1 0 100%;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-extraContent,
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-main {
  flex: 0 1 auto;
}
.RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-extraContent {
  margin-left: 88px;
  min-width: 282px;
}
@media screen and (max-width: 768px) {
  .RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-row {
    display: block;
  }
  .RA-pageWrapper .RA-pageWrapper-header .RA-pageWrapper-header-extraContent {
    margin-left: 16px;
  }
}
