.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-setting-contentControl .RA-setting-settingItem,
.RA-setting-layoutControl .RA-setting-settingItem {
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.RA-setting-contentControl .RA-setting-settingItem .RA-setting-settingItem-Label,
.RA-setting-layoutControl .RA-setting-settingItem .RA-setting-settingItem-Label {
  color: var(--text-color);
  font-weight: 400;
  font-size: 14px;
}
.RA-setting-contentControl .RA-setting-settingItem .RA-setting-settingAction,
.RA-setting-layoutControl .RA-setting-settingItem .RA-setting-settingAction {
  margin: 0;
  width: 80px !important;
}
.RA-setting-layoutMode img {
  border-radius: 5px;
  padding: 5px;
  box-shadow: 0 3px 8px 0 var(--shadow-color);
}
.RA-setting-layoutMode.RA-setting-layoutMode-disabled img {
  cursor: not-allowed;
}
.RA-setting-layoutMode .RA-setting-selectedIcon {
  top: 62px !important;
}
.RA-setting-themeChange {
  overflow: hidden;
  margin-bottom: 20px !important;
}
.RA-setting-themeChange .RA-setting-themeChange-colorBlock {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  color: #fff;
  font-weight: 700;
  text-align: center;
  border-radius: 2px;
  cursor: pointer;
  margin-bottom: 4px;
}
.RA-setting-drawer {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999;
  height: 100%;
}
.RA-setting-drawer.RA-setting-drawe-opened {
  width: 100% !important;
}
.RA-setting-drawer.RA-setting-drawe-opened .RA-setting-drawer-mask {
  height: 100%;
  opacity: 1;
  -webkit-transition: none;
  transition: none;
  -webkit-animation: raDrawerFadeIn 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
          animation: raDrawerFadeIn 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
}
.RA-setting-drawer.RA-setting-drawe-opened .RA-setting-drawer-wrapper {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.RA-setting-drawer.RA-setting-drawe-opened .RA-setting-icon {
  box-shadow: unset;
}
.RA-setting-drawer .RA-setting-icon {
  height: 52px;
  width: 48px;
  background-color: var(--navigator-background);
  position: absolute;
  right: 300px;
  top: 220px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  font-size: 18px;
  line-height: 56px;
  text-align: center;
  cursor: pointer;
  box-shadow: 0 3px 8px 0 var(--shadow-color);
  color: var(--primary) !important;
}
.RA-setting-drawer .RA-setting-drawer-mask {
  background-color: rgba(0, 0, 0, 0.45);
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  -webkit-transition: opacity 0.3s linear, height 0s ease 0.3s;
  transition: opacity 0.3s linear, height 0s ease 0.3s;
}
.RA-setting-drawer .RA-setting-drawer-wrapper {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  position: absolute;
  right: 0;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
  transition: transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
  transition: transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1), -webkit-transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
}
.RA-setting-drawer .RA-setting-drawer-wrapper .RA-setting-drawer-body {
  height: 100vh;
  max-height: 100vh;
  width: 300px;
  background-color: var(--navigator-background);
  padding: 24px;
  overflow-y: auto;
}
.RA-setting-drawer .RA-setting-drawer-wrapper .RA-setting-drawer-closeIcon {
  position: absolute;
  top: 24px;
  right: 24px;
  cursor: pointer;
  font-size: 16px;
  color: var(--text-color-secondary);
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  font-weight: bold;
  z-index: 998;
}
.RA-setting-drawer .RA-setting-drawer-wrapper .RA-setting-drawer-closeIcon:hover {
  color: var(--text-color);
}
@-webkit-keyframes raDrawerFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes raDrawerFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.RA-setting-Row {
  position: relative;
  margin-bottom: 24px;
}
.RA-setting-Row:last-child {
  margin-bottom: 0;
}
.RA-setting-Row .RA-setting-title {
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: bold;
}
.RA-setting-Row .RA-setting-title .RA-setting-title-tip {
  color: #c4c4c4;
  font-size: 12px;
  margin-left: 8px;
}
.RA-setting-Row.RA-setting-haveSelectedIcon img {
  margin-right: 12px;
  cursor: pointer;
  width: 48px;
  height: 42px;
}
.RA-setting-Row.RA-setting-haveSelectedIcon .RA-setting-selectedIcon {
  color: var(--primary);
  font-size: 16px;
  position: absolute;
  pointer-events: none;
  top: 58px;
  left: 28px;
}
.RA-setting-Row.RA-setting-haveSelectedIcon .RA-setting-selectedIcon.RA-setting-selectedIcon-rightPlace {
  left: 88px;
}
.Ra-setting-sampleImg {
  margin-right: 12px;
  cursor: pointer;
  width: 48px;
  height: 42px;
}
