.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-footer {
  color: var(--text-color-secondary);
  text-align: center;
}
.RA-footer.RA-footer-horizontal {
  margin-top: 16px;
}
.RA-footer-link {
  color: var(--text-color-secondary);
  cursor: pointer;
}
.RA-footer-link:hover {
  color: var(--primary);
}
